body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.page-wrapper {
  width: 100%;
  height: 100%;
  display: table;
  overflow: hidden;
}
.page-header,
.page-footer {
  display: table-row;
  height: 1px;
}
.page-content {
  display: table;
  width: 100%;
  height: 100%;
  table-layout: fixed;
}
.container {
  min-width: 320px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}
.btn {
  display: inline-block;
  vertical-align: top;
  border: 0;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  background: #000;
  color: #fff;
  text-align: center;
  font-size: fz_base;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
  transition: background 0.1s;
}
.btn:hover {
  text-decoration: none;
  background: #1a1a1a;
}
.btn:active,
.btn:focus {
  text-decoration: none;
  background: #262626;
}
.checkbox {
  position: relative;
}
.checkbox__inp {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.checkbox__ind:before {
  content: '';
  position: absolute;
}
.checkbox__ind:after {
  content: '';
  position: absolute;
}
.checkbox__inp:checked + .checkbox__ind:before {
  position: absolute;
}
.checkbox__inp:checked + .checkbox__ind:after {
  position: absolute;
}
.flights {
  position: relative;
}
.flights__table {
  width: 100%;
  box-shadow: 0 0 1px rgba(0,0,0,0.2);
  overflow: hidden;
  position: relative;
}
.flights__caption {
  font-size: 30px;
  padding: 20px 0;
  margin: 0;
  text-align: center;
  background: #fff;
}
.flights thead {
  background: #fff;
}
.flights td {
  text-align: left;
  position: relative;
}
.flights td,
.flights th {
  position: relative;
}
.flights td:hover:after,
.flights th:hover:after {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 1px #434343;
  left: 0;
  right: 0;
  top: -5000px;
  height: 10000px;
  z-index: 10;
  pointer-events: none;
}
.lte-ie9 .flights:after,
.no-js .flights:after,
.ie10 .flights:after {
  display: none;
}
.flights__head-cell {
  display: table-cell;
  padding: 16px 12px;
  text-align: left;
  font-weight: 700;
  border-bottom: 1px solid #e1e1e1;
  background: #fff;
}
.flights tbody {
  overflow: hidden;
}
.flights tbody tr {
  position: relative;
}
.flights tbody tr:nth-child(odd) td {
  background: #fafafa;
}
.flights tbody tr:hover td {
  box-shadow: 0 1px 0 #434343, inset 0 1px 0 #434343;
}
.flights__cell {
  padding: 8px 12px;
}
.flights__cell--type {
  width: 40px;
}
.flights__cell--time {
  width: 60px;
}
.flights__cell--comment {
  width: 220px;
}
.flights__company-logo {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
  overflow: hidden;
  text-decoration: none;
  margin-right: 8px;
}
.flights__company {
  display: inline-block;
  vertical-align: middle;
  max-width: 140px;
}
.flights__comment {
  font-size: 12px;
  line-height: 1;
  color: #999;
}
.flights__fixed {
  display: table;
  table-layout: fixed;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  min-width: 320px;
  max-width: 1200px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
  display: none;
}
.flights__fixed.active {
  display: block;
}
.radio {
  position: relative;
}
.radio__inp {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.radio__ind:before {
  content: '';
  position: absolute;
}
.radio__ind:after {
  content: '';
  position: absolute;
}
.radio__inp:checked + .checkbox__ind:before {
  position: absolute;
}
.radio__inp:checked + .checkbox__ind:after {
  position: absolute;
}
